body {
  margin: 0;
  padding: 0;
}

#root {
  display: flex;
  justify-content: center;
  -webkit-transform: translateY(60%);
          transform: translateY(60%);
}
.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 600px;
  background-color: rgba(0,0,0,.4);
  border-radius: 10%;
  color: rgb(223, 223, 223);
  box-shadow: 0px 0px 60px 10px black;
}

.App h1 {
  flex: 0 0 100%;
  text-align: center;
  font-size: 4em;
  margin-top: 0;
}

body {
  background-image: url(/pomodoro-clock/static/media/calm.c0682b14.jpg);
  background-size: 2600px 1733px;
  background-repeat: no-repeat;
  background-position: center;
}
.time-settings {
  margin-top: -50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex: 0 0 45%;
}

.time-settings h2 {
  width: 100%;
  height: 5px;
}

.time-settings #break-length {
  height: 30px;
  width: 30px;
  font-size: 25px;
}

.time-settings #session-length {
  height: 30px;
  width: 30px;
  font-size: 25px;
}

.time-settings button {
  background: none;
  border: none;
  height: 25px;
  font-size: 30px;
  color: rgb(223, 223, 223);
}

.time-settings button:active {
  color: grey;
}

.time-settings button:focus {
  outline: none;
}

.time-settings #session-label, #break-label {
  font-size: 30px;
}
.display {
  flex: 0 0 350px;
  text-align: center;
  height: 180px;
  border: 8px solid rgba(0,0,0,.7);
  box-shadow: 0px 0px 20px 2px black;
  border-radius: 50px;
}

.display #timer-label {
  font-weight: bold;
  font-size: 50px;
  margin-top: 10px;
}

.display #time-left {
  position: relative;
  font-size: 80px;
  height: 0;
  top: -80px;
}
.controller {
  flex: 0 0 100%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 10px;
}

.controller i {
  font-size: 40px;
}

.controller #reset {
  margin-left: 30px;
}

.controller span:hover {
  cursor: pointer;
}
